.intro__main {
  margin: 0 0 6.5rem;
  padding: 4.5rem 1.5rem;
  min-height: 40rem;
  background: url('../img/coverHeadBgrnd.jpg') no-repeat center;
  background-size: cover;
  border-radius: 9px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/coverHeadBgrnd@2x.jpg");
  }
}

.intro__main h1 {
  margin-bottom: 1.5rem;
  color: $white;
  font-size: 3.8rem;
  text-align: center;
}

.intro__main-content {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.3rem;
  font-weight: 600;
  text-transform: uppercase;

  img {
    margin-right: 1rem;
  }
}

.intro__main-content time {
  margin-left: 1rem;
  padding-left: 1rem;
  position: relative;

  &:before {
    content: '';
    display: block;
    width: 4px;
    height: 4px;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 50%;

    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: -2px;
  }
}

.intro__highlight {
  margin: 0 0 4rem 5rem;
  font-size: 2rem;
  font-weight: 600;
  max-width: 48.5rem;
  color: $purple;
  padding-left: 3rem;
  position: relative;

  &:before {
    content: '';
    width: 3px;
    height: 100%;
    background-color: $purple;
    border-radius: 2.5px;

    position: absolute;
    top: 0;
    left: 0;
  }
}

@media (max-width: 767px) {
  .intro__main h1 {
    font-size: 3.2rem;
  }

  .intro__main {
    padding: 4.5rem 1rem;
  }

  .intro__highlight {
    margin: 0 0 4rem;
    padding-left: 1.5rem;
  }
}