.footer {
  background-color: $white;
  padding: 2rem 0 3rem;

  ul {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  li a {
    font-size: 1.6rem;
    color: #7a7a7a;
    transition: color .2s linear;
  }

  a:hover {
    color: $purple;
  }

  ul:last-of-type {
    padding-top: 1.6rem;
    margin-top: 2.2rem;
    position: relative;

    &:before {
      content: '';
      display: block;
      width: 400px;
      max-width: 100%;
      background-color: #e9e9e9;
      height: 1px;

      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  ul:first-of-type li + li {
    margin-left: 3.2rem;
  }

  ul:last-of-type li + li {
    margin-left: 1.6rem;
  }
}

@media (max-width: 767px) {
  .footer {
    ul {
      display: block;
    }

    ul:first-of-type li + li,
    ul:last-of-type li + li {
      margin: 1rem 0 0;
    }
  }
}