.header {
  background-color: $white;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.header .container {
  max-width: 131rem;
}

.header__inner {
  display: flex;
  padding: 1.1rem 0;
  font-size: 1.6rem;
}

.header__logo {
  margin-right: 2rem;
  display: flex;
  align-items: center;
}

.header__logo span {
  font-size: 2.4rem;
  color: $black;
  font-weight: 500;
  margin-left: 8px;
}

.header {
  form {
    width: 30rem;
  }

  input {
    padding-right: 13.2rem;
    height: 4rem;
    font-size: 1.6rem;
  }

  button {
    height: 4rem;
    padding: 0 2.8rem;
    font-size: 1.6rem;
  }

  .trial-wrapper {
    margin-bottom: 0;
    margin-left: auto;
    padding: 0;
    border-radius: 0;
    background: none;
    box-shadow: none;
  }

  .trial {
    margin-right: 1.7rem;
    text-align: right;

    h4 {
      font-size: 1.6rem;
    }

    ul li {
      font-size: 1.1rem;
    }

    li + li {
      margin-left: 5px;
      padding-left: 5px;
    }

    li + li:before {
      width: 3px;
      height: 3px;
      left: -1.5px;
    }
  }
}

@media (max-width: 991px) {
  .header__inner {
    justify-content: space-between;
    align-items: center;
  }

  .header {
    .trial-wrapper {
      padding: 2.2rem 1.6rem;
      display: block;
    }

    .trial {
      margin-bottom: 1rem;
      text-align: left;
    }
  }
}

@media (max-width: 767px) {
  .header {
    .trial {
      h4 {
        text-align: left;
      }

      ul {
        justify-content: flex-start;
      }
    }

    form button {
      position: absolute;
      right: 0;
      top: 0;
      margin: 0;
      width: auto;
      border-radius: 0 6px 6px 0;
    }
  }
}