.method {
  padding: 4.8rem 0 5.6rem;
  color: $white;
  background-image: linear-gradient(128deg, #6190ee, #d362c4 98%);
}

.method h2 {
  margin: 0 auto 4.8rem;
}

.method__inner {
  margin-bottom: 5.6rem;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-gap: 3rem;
  padding-top: 5.6rem;
  border-top: 1px solid rgba(248, 247, 250, 0.22);
}

.method__img {
  position: relative;
  margin-right: -5rem;
}

.method__content {
  img {
    margin-bottom: 1.6rem;
  }

  p {
    margin: 0 0 16px;

    &:last-child {
      color: rgba(255, 255, 255, 0.6);
      margin-bottom: 0;
    }
  }

  h3 {
    color: $white;
    margin-bottom: 8px;
  }
}

.method__bottom {
  display: flex;
  justify-content: space-between;
  color: rgba(255, 255, 255, 0.6);

  .method__sign {
    font-size: 1.4rem;
  }
}

.method__sign {
  a {
    color: $white;
  }

  a svg {
    transition: transform .2s linear;
  }

  a:hover {
    svg {
      transform: translateX(5px);
    }
  }
}

.method__rating-wrapper {
  display: flex;
  align-items: center;
  font-size: 1.4rem;

  & + div {
    font-size: 1.4rem;
  }
}

.method__rating {
  margin-right: 0.5rem;
  width: 9rem;
  height: 1.5rem;
  background: url('../img/star.svg') repeat-x;
}

.method.method--mod {
  background: none;
  background-color: #e9e9e9;
  color: #7a7a7a;

  h2 {
    color: $purple;
  }

  .method__inner {
    margin-bottom: 0;
    border-top-color: #dddddd;
  }

  .method__content {
    .btn {
      margin-bottom: 3rem;
    }

    h3 {
      color: $black;
    }

    p {
      color: $black;
    }

    p:last-of-type {
      color: #7a7a7a;
    }
  }

  .method__rating {
    background-image: url('../img/star-purple.svg');
  }

  .method__rating-wrapper {
    color: $purple;
  }
}

@media (max-width: 991px) {
  .method__img {
    position: static;
    margin: 0;
  }

  .method__inner {
    grid-gap: 1rem;
  }
}

@media (max-width: 767px) {
  .method__inner {
    padding-top: 4rem;
    grid-template-columns: 1fr;
    grid-gap: 3rem;
  }

  .method h2 {
    margin-bottom: 4rem;
  }

  .method__bottom {
    display: block;

    .method__sign {
      margin-top: 2.4rem;
    }
  }
}