.btn--transparent {
  display: inline-flex;
  align-items: center;
  height: 4rem;
  background-color: transparent;
  border: 1px solid #a33ba3;
  border-radius: 6px;
  font-size: 1.5rem;
  color: #a33ba3;
  font-weight: 600; }
  .btn--transparent:hover {
    color: #ffffff; }

.input {
  display: block;
  max-width: 100%;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background: none;
  border: none;
  border-radius: 0;
  outline: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

.input-classic {
  width: 100%;
  height: 4.8rem;
  background-color: #ffffff;
  border: 1px solid #b8b8b8;
  border-radius: 6px;
  padding: 0 15rem 0 1.2rem;
  font-size: 1.8rem;
  color: #292622;
  transition: border-color .2s linear; }
  .input-classic::placeholder {
    color: #a3a3a3; }
  .input-classic:hover, .input-classic:focus {
    border-color: #292622; }

input._error {
  border-color: #f55858; }

main {
  margin-top: 9rem; }

section {
  margin-bottom: 8rem; }

form {
  max-width: 100%;
  width: 34.5rem;
  position: relative; }

form button {
  position: absolute;
  right: 0;
  top: 0; }

.ordered-list {
  margin-bottom: 4.8rem;
  counter-reset: title;
  list-style-type: none; }
  .ordered-list li {
    padding-left: 8rem;
    position: relative; }
    .ordered-list li::before {
      content: counter(title);
      counter-increment: title;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 1.7rem;
      font-size: 4rem;
      line-height: 1;
      color: #a33ba3;
      font-weight: 600; }

.list {
  margin-bottom: 4.8rem; }
  .list li {
    padding-left: 1.8rem;
    position: relative; }
    .list li:before {
      content: '';
      display: block;
      width: 6px;
      height: 6px;
      background-color: #a33ba3;
      border-radius: 50%;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%); }
  .list li + li {
    margin-top: 5px; }
  .list--purple li {
    color: #a33ba3;
    font-weight: 600; }
  .list--purple li:before {
    background-color: #292622; }

.note {
  margin-bottom: 4.8rem;
  background-color: #e9e9e9;
  padding: 2.4rem 2.6rem 2.4rem 8.1rem;
  border-left: 2px solid #a33ba3;
  color: #7a7a7a;
  font-size: 1.4rem; }
  .note span {
    color: #292622; }

.related {
  border-top: 1px solid #e9e9e9;
  padding-top: 6.4rem;
  margin-top: 6.4rem; }

@media (max-width: 991px) {
  main {
    margin-top: 7.7rem; }
  .ordered-list li {
    padding-left: 6rem; }
    .ordered-list li::before {
      left: 1rem;
      font-size: 3rem; }
  .note {
    padding: 2.4rem 2.6rem 2.4rem 4rem; }
  .related {
    padding-top: 4rem;
    margin-top: 4rem; } }

@media (max-width: 767px) {
  form {
    width: 100%; }
  form button {
    position: static;
    width: 100%;
    margin-top: 1.3rem; }
  .btn--classic {
    border-radius: 6px; }
  .input-classic {
    padding: 0 1.2rem; }
  .ordered-list li {
    padding-left: 3rem; }
    .ordered-list li::before {
      left: 0;
      top: 0.3rem;
      transform: none;
      font-size: 2.5rem; }
  .note {
    padding: 2rem; } }

