.nav-wrapper {
  display: flex;
  align-items: center;
  flex-grow: 1;
}

.nav ul {
  display: flex;

  a {
    display: block;
    color: $black;
    transition: color .2s linear, background-color .2s linear;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-weight: 500;
  }

  a:hover {
    background-color: #f8f7fa;
    color: $purple;
  }
}

@media (max-width: 991px) {
  .nav-wrapper {
    position: fixed;
    top: 57px;
    left: 0;
    width: 100%;
    height: calc(100% - 57px);
    z-index: 9;
    overflow: hidden;
    overflow-y: auto;
    transform: translateX(100%);
    transition: .3s ease-out;
    background-color: $white;
    display: block;

    &.active {
      transform: translateX(0);
    }
  }

  .nav ul {
    display: block;
  }

  .nav ul a {
    padding: 2rem 1.6rem;
    font-size: 1.8rem;
  }
}