*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%; /* 62.5% от 16px(дефолт знач шрифта в браузере) = 10px */
}

body {
  margin: 0;
  background-color: #f8f7fa;

  font-size: 1.8rem; /* 1.8 * 10px = 18px */
  font-weight: 400;
  line-height: 1.5;
  color: #7a7a7a;
  font-family: helvetica, arial, “PingFang SC”, “Microsoft YaHei”,”WenQuanYi Micro Hei”, sans-serif;

  &.lock {
    overflow: hidden;
  }
}

.container {
  width: 100%;
  max-width: 97rem;
  padding: 0 1.5rem;
  margin: 0 auto;

  &--md {
    max-width: 81rem;
  }

  &--sm {
    max-width: 65rem;
  }
}

.container .container--md,
.container .container--sm {
  padding: 0;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

h2 {
  color: $white;
  font-size: 2.4rem;
  max-width: 45rem;
  font-weight: 600;
  text-align: center;
  margin: 0 auto;
}

h3 {
  font-size: 2.4rem;
  color: $black;
  margin-bottom: 2.4rem;
}

p {
  margin: 0 0 2.4rem;
}

p span {
  color: $purple;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn {
  font-family: inherit;
  font-size: inherit;
  padding: 0;
  border: none;
  background: none;
  text-decoration: none;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.btn--classic {
  background-color: $purple;
  padding: 0 2.4rem;
  height: 4.8rem;
  color: $white;
  font-size: 1.8rem;
  border-radius: 0 6px 6px 0;
  transition: background-color .2s linear;

  &:hover {
    background-color: #7a0f7a;
  }
}

.btn--transparent {
  display: inline-flex;
  align-items: center;
  height: 4rem;
  background-color: transparent;
  border: 1px solid $purple;
  border-radius: 6px;
  font-size: 1.5rem;
  color: $purple;
  font-weight: 600;

  &:hover {
    color: $white;
  }
}

.input {
  display: block;
  max-width: 100%;
  font-family: inherit;
  font-size: inherit;
  color: inherit;

  background: none;
  border: none;
  border-radius: 0;
  outline: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.input-classic {
  width: 100%;
  height: 4.8rem;
  background-color: $white;
  border: 1px solid #b8b8b8;
  border-radius: 6px;
  padding: 0 15rem 0 1.2rem;
  font-size: 1.8rem;
  color: $black;
  transition: border-color .2s linear;

  &::placeholder {
    color: #a3a3a3;
  }

  &:hover,
  &:focus {
    border-color: $black;
  }
}

input._error {
  border-color: #f55858;
}

main {
  margin-top: 9rem;
}

section {
  margin-bottom: 8rem;
}

form {
  max-width: 100%;
  width: 34.5rem;
  position: relative;
}

form button {
  position: absolute;
  right: 0;
  top: 0;
}

.ordered-list {
  margin-bottom: 4.8rem;
  counter-reset: title;
  list-style-type: none;

  li {
    padding-left: 8rem;
    position: relative;

    &::before {
      content: counter(title);
      counter-increment: title;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 1.7rem;
      font-size: 4rem;
      line-height: 1;
      color: $purple;
      font-weight: 600;
    }
  }
}

.list {
  margin-bottom: 4.8rem;
  li {
    padding-left: 1.8rem;
    position: relative;

    &:before {
      content: '';
      display: block;
      width: 6px;
      height: 6px;
      background-color: $purple;
      border-radius: 50%;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  li + li {
    margin-top: 5px;
  }

  &--purple {
    li {
      color: $purple;
      font-weight: 600;
    }
    li:before {
      background-color: $black;
    }
  }
}

.note {
  margin-bottom: 4.8rem;
  background-color: #e9e9e9;
  padding: 2.4rem 2.6rem 2.4rem 8.1rem;
  border-left: 2px solid $purple;
  color: #7a7a7a;
  font-size: 1.4rem;

  span {
    color: $black;
  }
}

.related {
  border-top: 1px solid #e9e9e9;
  padding-top: 6.4rem;
  margin-top: 6.4rem;
}

@media (max-width: 991px) {
  main {
    margin-top: 7.7rem;
  }

  .ordered-list {

    li {
      padding-left: 6rem;

      &::before {
        left: 1rem;
        font-size: 3rem;
      }
    }
  }

  .note {
    padding: 2.4rem 2.6rem 2.4rem 4rem;
  }


  .related {
    padding-top: 4rem;
    margin-top: 4rem;
  }
}

@media (max-width: 767px) {
  form {
    width: 100%;
  }

  form button {
    position: static;
    width: 100%;
    margin-top: 1.3rem;
  }

  .btn--classic {
    border-radius: 6px;
  }

  .input-classic {
    padding: 0 1.2rem;
  }

  .ordered-list {

    li {
      padding-left: 3rem;

      &::before {
        left: 0;
        top: 0.3rem;
        transform: none;
        font-size: 2.5rem;
      }
    }
  }

  .note {
    padding: 2rem;
  }
}