.trial-wrapper {
  margin-bottom: 4rem;
  box-shadow: 0 20px 30px 0 rgba(85, 56, 152, 0.79);
  padding: 4rem 3.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f7fa;
  border-radius: 7px;

  &.has-bg {
    background: url('../img/coverFinalBgrnd.jpg') no-repeat center;
    background-size: cover;
    box-shadow: none;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/coverFinalBgrnd@2x.jpg");
    }

    h4 {
      color: $white;
    }

    li {
      color: $white;
    }

    li+li:before {
      background-color: $white;
    }
  }
}

.trial {
  margin-right: 4rem;

  h4 {
    font-weight: 600;
    color: $black;
    font-size: 2.4rem;
  }
}

.trial ul {
  display: flex;
  color: #7a7a7a;

  li {
    position: relative;
  }

  li + li {
    margin-left: 10px;
    padding-left: 10px;
  }

  li + li:before {
    content: '';
    display: block;
    width: 5px;
    height: 5px;
    background-color: #7a7a7a;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: -2.5px;
  }
}

.trial-bg {
  display: flex;
  flex-direction: column;
  background: url('../img/coverFinalBgrnd.jpg') no-repeat center;
  background-size: cover;
  min-height: 43.7rem;
  border-radius: 7px;
  padding: 13rem 8rem 2rem;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/coverFinalBgrnd@2x.jpg");
  }

  .method__sign {
    margin-top: auto;
    font-size: 1.6rem;
    color: rgba(255, 255, 255, 0.8);
    text-align: center;
  }
}

@media (max-width: 991px) {
  .trial-wrapper {
    padding: 3.6rem 1.9rem;
  }

  .trial-bg {
    padding: 6rem 1.9rem 2rem;
    //min-height: auto;
  }
}

@media (max-width: 767px) {
  .trial-wrapper {
    display: block;
  }

  .trial-bg {
    padding: 5rem 1rem 2rem;

    .method__sign {
      display: flex;
      flex-direction: column;
    }
  }

  .trial {
    width: 100%;
    margin-bottom: 2.4rem;

    h4 {
      text-align: center;
    }

    ul {
      justify-content: center;
    }
  }
}